import { NgModule } from '@angular/core'
import { CommonModule } from '@angular/common'

import { AkuntingRoutingModule } from './akunting-routing.module'
import { AkuntingComponent } from './akunting.component'
import { PerfectScrollbarModule } from 'ngx-perfect-scrollbar'
import { WidgetsComponentsModule } from 'src/app/components/widgets/widgets-components.module'
import { NzLayoutModule, NgZorroAntdModule } from 'ng-zorro-antd'
import { LayoutModule } from 'src/app/components/layout/layout.module'
import { KlaimComponent } from './klaim/klaim.component'
import { DetailKlaimComponent } from './detail-klaim/detail-klaim.component'
import { FormBuilderTypeSafe } from 'src/app/services/angular-reactive-forms-helper'
import { ReactiveFormsModule, FormsModule } from '@angular/forms'
import { ApprovalModule } from 'src/app/components/approval/approval.module'
import { DocumentModule } from 'src/app/components/document/document.module'
import { PipeModule } from 'src/app/pipe/pipe.module'

@NgModule({
    declarations: [AkuntingComponent, KlaimComponent, DetailKlaimComponent],
    imports: [
        CommonModule,
        AkuntingRoutingModule,

        PerfectScrollbarModule,
        WidgetsComponentsModule,
        NzLayoutModule,
        LayoutModule,
        NgZorroAntdModule,
        ReactiveFormsModule,
        FormsModule,
        ApprovalModule,
        DocumentModule,
        PipeModule
    ],
    providers: [FormBuilderTypeSafe],
})
export class AkuntingModule { }
