import { AkuntingComponent } from './akunting.component'
import { NgModule } from '@angular/core'
import { Routes, RouterModule } from '@angular/router'
import { KlaimComponent } from './klaim/klaim.component'
import { DetailKlaimComponent } from './detail-klaim/detail-klaim.component'

const routes: Routes = [
    {
        path: '',
        component: AkuntingComponent,
        children: [
            {
                path: 'reimburse',
                loadChildren: () =>
                    import('./reimburse/reimburse.module').then(mo => mo.ReimburseModule),
            },
            {
                path: 'billing',
                loadChildren: () => import('./billing/billing.module').then(mo => mo.BillingModule),
            },
            {
                path: 'expenses-summary',
                loadChildren: () => import('./expeses-summary/expeses-summary.module').then(mo => mo.ExpesesSummaryModule),
            },
            {
                path: 'klaim',
                component: KlaimComponent
            },
            {
                path: 'detail-klaim/:id_klaim_pengajuan',
                component: DetailKlaimComponent
            },
            {
                path: 'cash-advance',
                loadChildren: () => import('./cash-advance/cash-advance.module').then(mo => mo.CashAdvanceModule),
            },
            {
                path: 'pengaturan/billing-category',
                loadChildren: () => import('./pengaturan/billing-category/billing-category.module').then(mo => mo.BillingCategoryModule),
            },
            {
                path: 'pengaturan/untuk',
                loadChildren: () => import('./pengaturan/untuk/untuk.module').then(mo => mo.UntukModule),
            },
            {
                path: 'pengaturan/klaim',
                loadChildren: () => import('./pengaturan/klaim/klaim.module').then(mo => mo.KlaimModule),
            },

        ],
    },
]

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule],
})
export class AkuntingRoutingModule { }
